.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

ul{
  text-align: left;
}

.left{
  text-align: left;
}
body{
  background-color: white;
}

h1,h2{
  color:#E01B84;
}
h3, h4{
  color:#2F5496;
}
em{
  font-weight: bold;
}
footer{
  color:#E01B84;
}
.wrapper{
  width: 900px;
  padding: 10px;
  background-color: #FFF;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 10px 0 15px 5px rgba(0,0,0, 0.5);

}
body{
  margin: 0;
  background-color: #3465bb;
}